.hero-carousel{
    width: 100%;
    height: 100%;

}

.slide{
    width: 100%;
    background-size:100% 100%;
    background-size:cover;
    background-repeat: no-repeat;
    background-position: center center;
    display: flex;
    flex-direction: column;
    overflow: hidden;
    border-radius: 5px;
    height: 100%;
    width:100%;
}
.slide1{
    background-position-y: 0%;
}
.slide2{
    background-position-x: 21%;
}


.alice-carousel__stage-item{
    width: 100%;
}


.hero-slider .alice-carousel .alice-carousel__wrapper >.alice-carousel__stage >.alice-carousel__stage-item {
    height: 86vh;
}

@media only screen and (max-width: 414px) {
    .hero-slider .alice-carousel .alice-carousel__wrapper > .alice-carousel__stage > .alice-carousel__stage-item {
      height: 70vh;
    }
}

.alice-carousel__wrapper {
    position: relative;
    overflow-x: hidden;
    overflow-y: hidden;
    box-sizing: border-box;
    width: 100%;
}
.alice-carousel__prev-btn, .alice-carousel__next-btn{
 font-size: 20px;
 padding: 0;
 margin: 0;
}
.alice-carousel__dots, .alice-carousel__dots-item{
 font-size: 10px;
 padding: 0;
 margin: 0;
}
.alice-carousel__dots{
    margin-top: -20px;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);

}
.alice-carousel__dots-item{
    border: 1px solid rgb(255, 0, 55);
}

.alice-carousel__dots-item:not(.__custom):hover, .alice-carousel__dots-item:not(.__custom).__active{
    background-color: rgb(255, 40, 104);
}

@media screen and (max-width:414px) {
    .hero-slider>.alice-carousel{ 
        .alice-carousel__next-btn{
            right: 0px;
        }
        .alice-carousel__prev-btn, .alice-carousel__next-btn{
            // font-size: 20px;
            display: none;
        }
    }
    
}
@media screen and (max-height:414px) {
    .alice-carousel__prev-btn, .alice-carousel__next-btn{
        display: none;
    }
}
@media screen and (min-width:414px) {
    .hero-slider>.alice-carousel{ 
        .alice-carousel__next-btn{
            right: 8px;
        }
        .alice-carousel__prev-btn, .alice-carousel__next-btn{
            font-size: 30px;
        }
    }
}
    .hero-slider>.alice-carousel>{
        .alice-carousel__prev-btn, .alice-carousel__next-btn{
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            width: 50px;
            text-align: center;
            font-weight: bold;
            [data-area] {
                &::after {
                  font-family: FontAwesome;
                  font-style: normal;
                  font-weight: bold;
                  text-decoration: inherit;
                  text-transform: capitalize;
                  color: rgba(255, 255, 255, 0.767);
                }
            }
            &:hover [data-area]::after{
                color: rgb(255, 255, 255);
            }
        }   
        .alice-carousel__prev-btn{
            left: 0;
            [data-area] {
                &::after {
                    content:"\f0a8";
                }
            }
        }
        .alice-carousel__next-btn{
            [data-area] {
                &::after {
                    content:"\f0a9";
                }
            }
        }
    }
